.stage {
  background: radial-gradient(50% 50% at 50% 50%, #6A7984 0%, #444B4F 100%);
}
/* 血 */
.blood_box {
  width: 2.5rem;
  height: 3.375rem;
  left: 52%;
  transition-duration: 1000ms;
  bottom: 48%;
  transform: translate3d(-50%, -30vh, 0);
}
.blood_box:global(._move) {
  transform: translate3d(-50%, 0, 0);
}
.blood {
  box-shadow: 0px 0px 100px #FF0000;
}
.blood:global(._hide) {
  opacity: 0;
  transform: scale(0);
}
/* 书 */
.book_wrap {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  transition-duration: 400ms;
  transition-timing-function: ease-in-out;
  height: 40%;
  background-color: black;
}
.book_wrap:global(._hide) {
  transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.book {
  bottom: 100%;
}
.book:global(._shack) {
  animation: book-shake 200ms;
}
/* 按钮 */
.btn {
  bottom: 100%;
  margin-bottom: 8%;
  margin-left: -4%;
}
.btn_hand {
  transform: rotate(180deg);
  bottom: 150%;
  left: 110%;
}
/* 手 */
.hand {
  transform: translate3d(0, 0, 0);
  left: 5%;
}
.hand:global(._hide) {
  transform: translate3d(0, -100%, 0);
}
@keyframes book-shake {
  0%,
  100% {
    transform: translate3d(0, 0, 0);
  }
  30% {
    transform: translate3d(0, 10px, 0);
  }
}
