.flash {
  position: relative;
  z-index: 1;
}
.flash:before {
  content: "";
  position: absolute;
  background-color: #DC252D;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 100px;
  z-index: -1;
  animation: btn-flash 1s infinite;
}
@keyframes btn-flash {
  from {
    opacity: 1;
    transform: scaleX(1) scaleY(1);
  }
  to {
    opacity: 0;
    transform: scaleX(1.1) scaleY(1.3);
  }
}
