.stage {
  background-color: #3c0f0c;
}
.exp {
  top: 8%;
  transform: translate3d(-50%, 0, 0);
  opacity: 1;
  visibility: visible;
}
.exp:global(._hide) {
  transform: translate3d(-50%, -100%, 0);
  opacity: 0;
  visibility: hidden;
}
.exp:global(._leave) {
  opacity: 0;
  transform: translate3d(-50%, 100%, 0);
  visibility: hidden;
  transition-delay: 100ms;
}
