.btn {
  left: 43%;
  top: 24%;
  transform: translate(-50%, -50%);
}
.clickHit {
  margin-top: 0.4rem;
  margin-left: -3.5rem;
}
.clickHit:global(._hit_it) {
  animation: hit 1s;
}
@keyframes hit {
  0% {
    transform: translate3d(-60px, 0, 0);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}
