.wrap {
  width: 16.6875rem;
  height: 1.25rem;
  background: linear-gradient(90deg, #000000 0%, #403737 100%);
  top: 48%;
  transform: translate3d(-50%, 0, 0);
}
.wrap:global(._hide) {
  opacity: 0;
  transform: translate3d(-50%, 30px, 0);
}
.box {
  background-image: url("6ce63a697baca2972a8269d4ee6f9562");
  background-position: 0 0;
  background-size: auto 100%;
}
.box img {
  left: 100%;
  transform: translate(-50%, -50%);
  height: 150%;
}
.number {
  bottom: 100%;
  margin-bottom: 0.5rem;
  right: 0;
}
