body, html {
  margin: 0;
  padding: 0;
  font-size: 16px;
  background-color: #000000;
  font-family: 'system-ui', '—apple-system', 'Segoe UI', 'Rototo', 'Emoji', 'Helvetica', 'Arial', 'sans-serif';
}
h1, h2, h3, h4, h5, h6{
  margin: 0;
  padding: 0;
}

body, html, #root {
  height: 100%;
}

strong, em, i, body, h1, h2, h3, h4, h5, small {
  font: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}
