.wrap {
  transform: translate3d(0, 100%, 0);
  opacity: 0;
}
.box {
  border: 3px solid #A42E2A;
  z-index: 2;
}
.box:before,
.box:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 2px;
}
.box:before {
  top: -7px;
  left: -7px;
  padding: 5px;
  border: 2px solid rgba(164, 46, 42, 0.8);
}
.box:after {
  top: -10px;
  left: -10px;
  padding: 9px;
  border: 1px solid rgba(164, 46, 42, 0.5);
}
.box .text {
  margin-left: -1rem;
  margin-right: -0.5rem;
}
.text {
  opacity: 0;
  transform: translate3d(2rem, 0, 0);
  background: linear-gradient(90deg, rgba(182, 193, 244, 0.6) 0%, rgba(182, 193, 244, 0));
}
.foot {
  opacity: 0;
  transform: translate3d(-2rem, 0, 0);
}
.wrap:global(._show) {
  transform: translate3d(0, 0, 0);
  opacity: 1;
}
.wrap:global(._show) .text,
.wrap:global(._show) .foot {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
/**/
.wrap:global(._red):before {
  content: "";
  position: absolute;
  left: -0.5rem;
  bottom: -1rem;
  width: 100%;
  height: 120%;
  padding: 1.5rem 0;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
.wrap:global(._red) .box {
  background-color: transparent;
}
.wrap:global(._red) .text {
  color: #ffffff;
  background: linear-gradient(90deg, rgba(255, 0, 0, 0.6) 0%, rgba(104, 22, 27, 0) 98.01%);
}
