strong.btn:global(._block) {
  width: 75%;
  max-width: 20rem;
}
.main h2 {
  text-shadow: 0px 0px 1rem #FF0000;
}
.bg {
  transform: translate3d(0, 50%, 0) scale(2);
  transition-timing-function: cubic-bezier(0.46, 0.03, 0.52, 0.96);
}
.bg:global(._in) {
  transform: translate3d(0, 0, 0) scale(1);
}
