@import "sacss/mixin.less";

#Margins(4, 8, 16, 20, 24, 32; 1/16*1rem);
#Paddings(8, 12, 16, 24; 1/16*1rem);
#SACSS(font-size; fs; 12, 14, 16, 24; 1/16*1rem);
#SACSS(line-height; lh; 16, 24; 1/16*1rem);
#SACSS(border-radius; br; 2, 8, 32, 40; 1/16*1rem);
#SACSS(height; h; 48; 1/16*1rem);


.r50\% {
  right: 50%;
}

.td200 {
  transition-duration: 200ms;
}

.td300 {
  transition-duration: 300ms;
}

.td600 {
  transition-duration: 600ms;
}

.tde100 {
  transition-delay: 100ms;
}

.tde200 {
  transition-delay: 200ms;
}

.tde300 {
  transition-delay: 300ms;
}

.tde400 {
  transition-delay: 400ms;
}

.tde500 {
  transition-delay: 500ms;
}

.tde600 {
  transition-delay: 600ms;
}

.clearfix {
  #Clearfix;
}

.ell {
  #Ellipsis;
}

.tt-50\%{
  transform: translate(-50%,-50%);
}
.ttX-50\%{
  transform: translateX(-50%);
}
.ttY-50\% {
  transform: translateY(-50%);
}

.ls0\.9 {
  letter-spacing: 0.9px;
}

.bsc {
  background-size: cover;
}

.ha {
  height: auto;
}

