.mask {
  transform: rotate(90deg);
  opacity: 0;
}
.mask:global(._show) {
  transform: rotate(27deg);
  opacity: 1;
}
.mask:global(._leave) {
  transform: rotate(90deg);
  opacity: 0;
}
.left,
.right {
  height: 150%;
  top: -25%;
}
.left {
  transform: translate3d(-100%, 0, 0);
}
.right {
  transform: translate3d(100%, 0, 0);
}
.mask:global(._show) .left {
  transform: translate3d(0, 0, 0);
}
.mask:global(._show) .right {
  transform: translate3d(0, 0, 0);
}
.mask:global(._leave) .left {
  transform: translate3d(-100%, 0, 0);
}
.mask:global(._leave) .right {
  transform: translate3d(100%, 0, 0);
}
