.wrap {
  animation: upDown 800ms ease-in infinite;
}
.hand_box {
  transform: rotate(-90deg) scaleY(-1);
}
.angle {
  top: 19px;
  margin-left: 4px;
}
.text {
  color: #DABA6E;
  left: 55px;
  top: 20px;
  transform: scale(0.9);
  transform-origin: 0 50%;
}
@keyframes upDown {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
}
